import { useState } from 'react';
import { Animate } from './components/Animate';
import Animations from './components/Animations';

function App() {
  const style = getStyle();
  const [key, setKey] = useState(0);
  const [anim, setAnim] = useState({from: {opacity: 0}, to: [{opacity: 1}]});

  return (
    <div style={style.mainDiv}>
      <div style={style.leftDiv} key={key}>  
        <Animate 
        style={style.leftDiv.div}
        from={anim.from} 
        to={anim.to}>
          <a style={{color: '#12e1f3', textDecoration: 'none'}}
          href="https://www.npmjs.com/react-animation-maker" 
          target="blank">
            See Documentations
          </a>
        </Animate>
      </div>

      <div style={style.rightDiv}>
        {Object.entries(Animations).map(([listName, listAnims]) => (
          <div style={style.rightDiv.listDiv}>
            <label style={style.rightDiv.header}>
              {listName}
            </label>
            <div style={style.rightDiv.btnsDiv}>
              {Object.entries(listAnims).map(([animName, animObj]) => (
                
                <button style={style.rightDiv.btn}
                onClick={() => {
                  setAnim(animObj);
                  setKey((prev) => prev+1);
                }}>
                  {animName}
                </button>

              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default App;

const getStyle = () => ({
  mainDiv: {
    display: 'flex',
    flexDirection: (document.body.clientWidth >= 720) ? 'row' : 'column',
    height: '100vh',
    overflow: 'hidden',
    fontFamily: 'cursive',
  },
  leftDiv: {
    flex: (document.body.clientWidth >= 1150) ? 2 : 1,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#f6f6f6',

    div: {
      padding: '5em',
      borderRadius: '20px',
      backgroundColor: '#12a1a3',
      fontFamily: 'Arial',
    }
  },
  rightDiv: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    backgroundColor: '#dedede',
    overflow: 'auto',

    listDiv: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'flex-start',
      padding: '20px',
    },
    header: {
      padding: 10,
      borderRadius: 10,
      color: '#333',
      backgroundColor: '#ccc',
    },
    btnsDiv: {
      width: 300,
      padding: 10,
    },
    btn: {
      width: '100%',
      padding: 20,
      margin: 2,
      border: 0,
      color: '#111',
      borderRadius: 5,
      boxShadow: '0 0 4px #00000022',
      backgroundColor: '#eee',
      cursor: 'pointer',
      fontFamily: 'Arial',
    },
  },
})